<template>
  <v-row>
    <v-col>
      <v-data-table
        :items="items"
        :items-per-page="5"
        :headers="headers"
        item-key="id"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title
              ><v-btn color="warning" text plain to="/users">Index</v-btn>/<span
                class="text--disabled ml-3 subtitle-1"
                >{{ $route.name }}</span
              ></v-toolbar-title
            >
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialogDelete" max-width="350px">
              <v-card>
                <v-card-title class="text-h5 text-break text-center"
                  >Are you sure you want to delete this item?</v-card-title
                >
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="error" small text @click="closeDelete"
                    >Cancel</v-btn
                  >

                  <v-btn color="success" small @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="dialogEdit" max-width="350px">
              <v-card>
                <v-card-title class="text-h5 text-break text-center">
                  <span class="text-h5">Edit Client</span>
                </v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-select
                        v-model="selectedCustomer"
                        :items="customers"
                        return-object
                        label="Organization"
                        item-text="name"
                      ></v-select>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="error" small text @click="closeEdit"
                    >Cancel</v-btn
                  >
                  <v-btn color="success" small @click="editConfirm">OK</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>

        <template v-slot:item.index="{ index }"> {{ index + 1 }}. </template>
        <template v-slot:item.customerId="{ item }">
          {{ getCustomerNameFromId(item.customerId) }}
        </template>
        <template v-slot:item.lastSeen="{ item }">
          {{ dt.fromMillis(item.lastSeen).toRelative() }}
        </template>
        <template v-slot:item.cpuUsage="{ item }">
          <v-progress-linear
            :color="handleColor(item.cpuUsage)"
            background-color="grey lighten-1"
            :value="item.cpuUsage"
          ></v-progress-linear>
          {{ item.cpuUsage + "%" }}
        </template>
        <template v-slot:item.memUsage="{ item }">
          <v-progress-linear
            :color="handleColor(item.memUsage)"
            background-color="grey lighten-1"
            :value="item.memUsage"
          ></v-progress-linear>
          {{ item.memUsage + "%" }}
        </template>
        <template v-slot:item.netUsage="{ item }">
          <v-progress-linear
            :color="handleColor(item.netUsage)"
            background-color="grey lighten-1"
            :value="item.netUsage"
          ></v-progress-linear>
          {{ item.netUsage + "%" }}
        </template>
        <template v-slot:item.serviceStatus="{ item }">
          <v-switch
            @change="updateClient(item)"
            v-model="item.serviceStatus"
            :label="getLabel(item.serviceStatus)"
            color="success"
          ></v-switch>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon color="primary" small class="mr-2" @click="openEdit(item)">
            mdi-pencil
          </v-icon>
          <v-icon color="error" small @click="deleteItem(item)">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
</template>

<script>
import { tableMixin } from "@/mixins/tableMixin";
import DmInput from "@/components/common/form/DmInput";

export default {
  name: "clients",
  mixins: [tableMixin],
  components: { DmInput },
  data: () => ({
    itemName: "Client",
    headers: [
      { value: "index", text: "#" },
      { value: "id", text: "Client UUID" },
      { value: "globalIp", text: "Device Ip" },
      { value: "customerId", text: "Customer" },
      { value: "lastSeen", text: "Last Seen" },
      { value: "cpuUsage", text: "Cpu Usage" },
      { value: "memUsage", text: "Memory Usage" },
      { value: "netUsage", text: "Network Usage" },
      { value: "serviceStatus", text: "ICSPOT Service Status" },
      { value: "actions", text: "Actions", sortable: false },
    ],
    editedClient: {},
    selectedCustomer: {},
    dialogEdit: false,
    customers: [],
  }),
  methods: {
    handleColor(val) {
      if (val < 50) {
        return "success";
      } else if (val > 51 && val < 79) {
        return "yellow darken-2";
      } else {
        return "error";
      }
    },
    openEdit(client) {
      this.editedClient = client;
      this.selectedCustomer = this.getCustomerFromId(
        this.editedClient.customerId
      );
      this.dialogEdit = true;
    },
    closeEdit() {
      this.editedClient = {};
      this.selectedCustomer = {};
      this.dialogEdit = false;
    },
    editConfirm() {
      this.editedClient.customerId = this.selectedCustomer.id;
      Promise.all([this.$axios.post("secured/saveClient", this.editedClient)])
        .then((values) => {
          this.$toast.setMessage("Successfully Saved", 2);
        })
        .catch(({ response }) => {
          this.$toast.setMessage(response, 1);
        });
      this.closeEdit();
    },
    getCustomers() {
      Promise.all([this.$axios.post("secured/getCustomerList")])
        .then((values) => {
          this.customers = values[0].data;
        })
        .catch(({ response }) => {
          this.$toast.setMessage(response.data, 1);
        });
    },
    getCustomerFromId(customerId) {
      if (this.customers === undefined || this.customers.length === 0) {
        this.getCustomers();
      } else {
        let cstmr = this.customers.find(
          (customer) => customer.id === customerId
        );
        return cstmr;
      }
    },
    getCustomerNameFromId(customerId) {
      let x = this.getCustomerFromId(customerId);
      if (x === undefined) {
        return "";
      } else {
        return x.name;
      }
    },
    getLabel(serviceStatus) {
      return serviceStatus ? "Active" : "Inactive";
    },
    updateClient(client) {
      console.log(client)
      Promise.all([this.$axios.post("secured/saveClient", client)])
        .then((values) => {
          this.$toast.setMessage("Successfully Saved", 2);
        })
        .catch(({ response }) => {
          this.$toast.setMessage(response.data, 1);
        });
    },
  },
};
</script>
